
import VHeader from '@/components/v-header/v-header'
import Scroll from '@/components/scroll/scroll'
import Badge from 'vux/src/components/badge/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import axios from '@/api/axiosApi'
import { QINIU_PREFIX } from '@/api/config'
import defaultAvatar from '@/assets/img/default_avatar.png'
import { mapGetters, mapMutations } from 'vuex'
import noticeImage from '@/assets/img/notice.png'
import { personifyTime } from '@/assets/js/utils'

export default {
name: 'MessageList',
data () {
return {
conversationList: [],
listTab: ['私信', '通知'],
swiperHeight: 500 + 'px',
index: 0,
noticeImage: noticeImage
}
},
components: {
VHeader, Scroll, Badge, Tab, TabItem, Swiper, SwiperItem
},
created () {
this._getConversationList()
},
mounted () {
const _self = this
_self.$refs.scroll.$el.style.height = window.innerHeight - this.$refs.scroll.offsetTop + 'px'
_self.$refs.noticeScroll.$el.style.height = window.innerHeight - this.$refs.noticeScroll.offsetTop + 'px'
this.swiperHeight = window.innerHeight - 40 - 44 + 'px'
},
computed: {
...mapGetters([
'syncConversation'
]),
...mapGetters({
selected: 'messageTab'
})
},
methods: {
...mapMutations({
setMessageTab: 'SET_MESSAGE_TAB'
}),
_getConversationList () {
const JIM = this.$store.getters.JIM
const _self = this
JIM.getConversation().onSuccess(function(msg) {
msg.conversations.sort((o1, o2) => o1.mtime < o2.mtime)
let tmp = []
msg.conversations.forEach(item => {
let tmpItem = {}
tmpItem.id = item.username
tmpItem.nickname = item.nickName

tmpItem.unreadMsgCount = item.unread_msg_count > 99 ? 99 : item.unread_msg_count

tmpItem.lastedMsg = ''
tmpItem.lastedMsgTime = ''
tmpItem.headUrl = ''
_self.syncConversation.forEach(item1 => {
if (item1.from_username === item.name && item1.msgs) {
item1.msgs.forEach((msg1, index) => {
tmpItem.lastedMsg = msg1.content.msg_body.text
tmpItem.lastedMsgTime = personifyTime(msg1.content.create_time)
})
}
})
tmp.push(tmpItem)
_self._getAvatar(tmpItem.id)
})
_self.conversationList = tmp

})
},
_getAvatar (id) {
axios.get(this, `/v1/userInfo/${id}`, null, (data) => {
if (data.headUrl) {
if (data.headUrl.indexOf(QINIU_PREFIX) > -1) {
data.headUrl += '-bheader'
}
} else {
data.headUrl = defaultAvatar
}
this.conversationList.forEach(item => {
if (item.id === data.id) {
item.headUrl = data.headUrl
item.nickname = data.nickname
}
})
})
},
toChat (obj) {
this.$store.dispatch('setChatObject', obj)
this.$router.push('/chat')
},
selectTabEvery (value) {
this.setMessageTab(value)
}
},
watch: {
'$route'(to, from) {
if (to.name === 'MessageList') {
this._getConversationList()
}
}
}
}



